import React from "react";
import "./style/intro.css";
import photo from "./style/photo.jpg";

function Intro() {
  return (
    <div className="intro">
      <div className="welcome-div">
        <code>
          &lt;h3 style= &#123;&#123; fontFamily: "Kaushan Script" &#125;&#125;
          &gt;
          <h3 style={{ fontFamily: "Kaushan Script" }}> Hello There ! </h3>
          &lt;/h3&gt;
        </code>
        <code>
          &lt;h1&gt; &lt;strong&gt; &lt;i&gt;
          <h1>
            <i> Sourav Koley here </i>
          </h1>
          &lt;/i&gt; &lt;/strong&gt; &lt;/h1&gt;
        </code>
        <code>
          &lt;h5 style= &#123;&#123;color: '#94F618' &#125;&#125;&gt; &lt;i&gt;
          <h5>
            <i> A Web developer focusing on front-end from Kolkata </i>
          </h5>
          &lt;/i&gt;&lt;/h5&gt;
        </code>

        <code>
          &lt;button style= &#123;&#123;backgroundColor: '#18F6F6'
          &#125;&#125;&gt;
          <a href="#contact">
            <button
              style={{
                height: "33px",
                width: "100px",
                margin: "10px",
                backgroundColor: "#18F6F6",
                border: "none",
                borderRadius: "6px",
                fontSize: "14px",
              }}
            >
              Contact Me
            </button>
          </a>
          &lt;/button&gt;
        </code>
      </div>
      <div className="photo-div">
        <code>&lt;img src= </code>
        <img src={photo} alt="loading" id="photo" />
        <code style={{ color: "#18F6F6" }}>
          /&#42; A good developer will always specify size of the image &#42;/
        </code>
        <code>width = '40%'</code>
        <code>/ &gt;</code>
      </div>
    </div>
  );
}

export default Intro;
