import React from "react";
import "./style/contact.css";

function Contact() {
  return (
    <div
      id="contact"
      style={{
        width: "100%",
        backgroundColor: "#d2d9e2",
        display: "flex",
        justifyContent: "space-evenly",
        alignItems: "center",
        padding: "70px 20px 20px 20px",
        position: "relative",
      }}
    >
      <h3
        style={{
          position: "absolute",
          top: "17px",
          fontWeight: "500",
          fontSize: "22px",
        }}
      >
        <u>Contact Me</u>{" "}
      </h3>
      <form
        action="https://formsubmit.co/koley.sourav.work@gmail.com"
        method="POST"
      >
        <div>
          <textarea
            name="message"
            placeholder="Write your content here"
            required
          ></textarea>
        </div>
        <div>
          <input type="text" name="name" placeholder="Your name" required />
          <input
            type="email"
            name="email"
            placeholder="Your mail address"
            required
          />
          <button type="submit">Send</button>
        </div>
      </form>
    </div>
  );
}

export default Contact;
